.burger-nav {
  justify-content: flex-start;
  align-items: flex-start;
  position: sticky;
  top: 0;
  min-height: 100vh;
  width: 288px;
  display: flex;
  flex-direction: column;
  margin: auto;
  margin-top: 76px;
}

.burger-nav__list {
  width: 100%;
  padding: 8px 0;
  padding-left: 0;
  list-style: none;
}

.burger-nav__item {
  width: 100%;
  display: flex;
  justify-content: center;
}

.burger-nav__link {
  color: #89939a;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 11px;
  line-height: 27px;
  display: inline-block;
  &.active {
    color: #0f0f11;
    position: relative;

    &::after {
      content: '';
      width: 100%;
      position: absolute;
      left: 0;
      bottom: 0;
      border-radius: 8px;
      height: 2px;
      background-color: #0f0f11;
    }
  }
}

.burger-nav__item:not(:last-child) {
  margin-bottom: 16px;
}

.nav-link:hover {
  color: #0f0f11;
}

.burger-icon {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 0;

  .burger-icon__box {
    background-color: white;
    flex: 1;
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 1px solid #e2e6e9;
    border-top: 1px solid #e2e6e9;

    &.active {
      border-bottom: 2px solid #0f0f11;
    }
  
  }
    &:last-child {
      border-right: none;
    }


  }

  .burger-icon__img {
    width: 16px;
    height: 16px;
    margin: 24px auto;
  }


@media (min-width: 550px) {
  .burger {
    display: none;
  }
}
